.acordion {
  @media (max-width: $mobile-width) {
    &--close {
      & .acordion__body {
        display: none;
      }

      & .acordion__toggle {
        &::after {
          transform: rotate(90deg);
        }
      }
    }

    &--nojs {
      & .acordion__body {
        display: block;
      }

      & .acordion__toggle {
        display: none;
      }
    }
  }
}

.acordion__toggle {
  position: absolute;
  top: 0;
  right: 3px;

  display: none;

  width: 16px;
  height: 16px;

  border: none;
  background-color: transparent;

  @media (max-width: $mobile-width) {
    display: block;

    &::before,
    &::after {
      position: absolute;
      top: 50%;
      left: 0;

      width: 100%;
      height: 2px;

      content: "";
      transition: transform 0.3s;

      background-color: #c4c4c4;
    }
  }
}
